@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.home {
  height: 100vh;
  background-image: url(/programmer-profile/static/media/home-bg.8d7c6388.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}
.black-container {
  background-color: rgba(0, 0, 0, 0.589);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.home-container {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}
.avatar {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: #060606;
  text-align: center;
  display: block;
  border: 2px solid #585855;
  margin: 40px auto;
}
.avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.home-container h1 {
  color: rgb(255, 255, 255);
  font-size: 34px;
  margin-bottom: 40px;
  text-transform: capitalize;
}
.home-container p {
  font-size: 22px;
  margin-bottom: 40px;
  color: rgb(175, 175, 175);
  line-height: 1.8;
}
.home-container .contact-me {
  padding: 15px;
  background: #22262d00;
  border: 1px solid rgb(255, 255, 255);
  font-size: 18px;
  color: white;
  cursor: pointer;
  font-weight: bold;
  text-transform: capitalize;
  margin: 40px 20px;
}
.home-container .contact-me:hover {
  background-color: #fff;
  color: #333;
}

.navbar {
  background-color: rgba(0, 0, 0, 0);
  padding: 20px 0;
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  -webkit-transition-property: padding, background-color;
  transition-property: padding, background-color;
  -webkit-transition-duration: 0.7s;
          transition-duration: 0.7s;
}
.container {
  width: 85%;
  margin: auto;
}
.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar-container .logo {
  width: 50%;
  font-weight: bold;
  font-size: 20px;
  text-transform: capitalize;
  color: #fff;
}
.navbar-container .navbar-list {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar-container .navbar-list a {
  padding: 10px 15px;
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-transform: capitalize;
  font-size: 18px;
  cursor: pointer;
}
.navbar-container .navbar-list a:after {
  content: "";
  display: block;
  height: 2px;
  width: 0;
  background-color: #ffffff;
  margin-top: 3px;
  -webkit-transition: width 0.6s;
  transition: width 0.6s;
}
.navbar-container .navbar-list a:first-child:after {
  content: "";
  display: block;
  height: 2px;
  width: 100%;
  background-color: #ffffff;
  margin-top: 3px;
}
.navbar-container .navbar-list a:hover:after {
  width: 100%;
}
.navbar-container span {
  cursor: pointer;
  font-size: 1.5em;
  padding: 20px;
  display: none;
  color: #989898;
}

.navbareffectUp {
  padding: 20px 0;
  background-color: rgba(255, 255, 255, 0);
  color: rgb(0, 0, 0);
}
.navbareffect {
  padding: 10px 0;
  background-color: rgb(255, 255, 255);
  border-bottom: 1px solid #333;
}
.showNavbar {
  /* right: 0; */
  display: block;
}

@media screen and (max-width: 650px) {
  .navbar-container .navbar-list {
    display: none;
  }
  .navbar-container {
    justify-content: space-between;
  }
  .hidden-list {
    width: 60%;
  }
  .navbar-container span {
    display: block;
  }
}

.portfolio-list {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 30px 0;
}
.portfolio-list li {
  text-decoration: none;
  padding: 10px 30px;
  list-style: none;
  text-transform: capitalize;
  cursor: pointer;
}
.active {
  background-color: #333;
  color: #fff;
}
.porfolio-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.porfolio-row div {
  width: 25%;
  font-size: 0;
}
.porfolio-row img:hover {
  opacity: 0.8;
}
.porfolio-row img {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 650px) {
  .porfolio-row div {
    width: 100%;
    border: 1px solid #fff;
  }
  .profile-left ul li span {
    width: 90px;
  }
  .portfolio-list li {
    padding: 10px 10px;
    font-size: 16px;
  }
  .portfolio-list {
    justify-content: space-evenly;
  }
}

.contact {
  background-color: #f8f8f8;
}
.container-contact {
  width: 85%;
  text-align: center;
  margin: auto;
}
.container-contact form {
  margin-top: 60px;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}
#name,
#email,
#subject,
#submit,
textarea {
  padding: 10px;
  margin-bottom: 12px;
  border: 1px solid #8c8c8c;
}
form .form-input {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
form .form-input > div {
  width: 49%;
}
#name,
#email {
  width: 100%;
}
#subject {
  width: 100%;
}
textarea {
  width: 100%;
  height: 200px;
  resize: none;
  background-color: #ccc;
}
#submit {
  /* width: 40%; */
  padding: 10px 20px;
  background-color: rgb(39, 39, 39);
  margin: auto;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}
.errors {
  display: flex;
}
.error {
  width: 100%;
  font-size: 12px;
  color: red;
}

@media screen and (max-width: 650px) {
  form .form-input > div {
    width: 100%;
  }
  #submit {
    width: 50%;
  }
  #name,
  #email,
  #submit,
  textarea {
    padding: 12px;
  }
  .container-contact form {
    width: 100%;
  }
}

.profile-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  color: #333;
}
.profile-left {
  width: 50%;
}
.profile-left h4 {
  margin-bottom: 20px;
}
.profile-left .socialRow {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 12px;
}
.profile-left li .icons {
  margin-right: 30px;
}
.socialRow .icons {
  display: inline-block;
  text-align: center;
  font-size: 20px;
  color: #1a1a1a;
  border: 1px solid #000;
  border-radius: 50%;
  line-height: 36px;
  width: 38px;
  height: 38px;
}
.profile-right {
  width: 50%;
}
.bar {
  height: 5px;
  background-color: rgb(189, 188, 188);
}
.bar1,
.bar2,
.bar3,
.bar4 {
  height: 100%;
  background-color: #333;
  -webkit-transition: width 2s;
  transition: width 2s;
  width: 0;
}
.bar1-effect {
  width: 90%;
}
.bar2-effect {
  width: 90%;
}
.bar3-effect {
  width: 80%;
}
.bar4-effect {
  width: 70%;
}

.bar {
  margin-top: 10px;
}
.profile-right ul {
  padding: 15px 0;
}
.profile-right ul li {
  position: relative;
}
.profile-left p {
  font-size: 15px;
  line-height: 2em;
  width: 80%;
  margin: 20px 0;
}
.info-icon {
  margin-right: 5px;
}
.profile-left li {
  padding: 10px 0;
  font-size: 14px;
}
.profile-right li {
  padding: 20px 0;
  font-size: 14px;
  margin: 20px 0;
}
.skill,
.percentage {
  position: absolute;
  top: 0;
  font-weight: bold;
  font: 18px;
}
.skill {
  left: 0;
  text-transform: capitalize;
}
.percentage {
  right: 0;
}
.profile-left ul li span {
  display: inline-block;
  width: 110px;
  font-weight: bold;
  text-transform: capitalize;
}

@media screen and (max-width: 650px) {
  .profile-left li {
    font-size: 14px;
  }
  .profile-right ul li span {
    font-size: 14px;
  }
  .profile-right li {
    padding: 10px 0;
    font-weight: bold;
  }
  .bar {
    margin-bottom: 40px;
    height: 5px;
  }
  .socialIcon {
    font-size: 16px;
  }

  .profile-left p {
    width: 100%;
    margin-bottom: 20px;
  }
  .three-bars {
    display: block;
  }
  .profile-left .socialRow {
    justify-content: center;
  }
}

.service {
  background-color: #f8f8f8;
}
.service-row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 20px 0;
}
.service-row > div {
  border: 1px solid #888;
  padding: 60px 20px;
  text-align: center;
  width: 30%;
  margin: 0 1%;
}
.service-row > div > div {
  margin-bottom: 20px;
}
.service-row h2 {
  font-size: 25px;
  color: #333;
  text-transform: capitalize;
}
.service-row p {
  font-size: 14px;
  color: #888;
}
.service-row span {
  font-size: 1.5em;
}
.middle-div {
  margin: 0 5%;
}
.line {
  background-color: #888;
  width: 60%;
  margin: auto;
  margin-bottom: 40px;
}

@media screen and (max-width: 650px) {
  .service-row > div {
    width: 100%;
    margin: 20px;
    padding: 20px;
  }
  .profile-left {
    width: 100%;
    margin-top: 20px;
  }
  .profile-right {
    width: 100%;
    margin-top: 20px;
  }
  .about-second {
    width: 100%;
  }
}

.footer {
  background-color: rgb(19, 14, 14);
  text-align: center;
}
.footer p {
  color: rgb(87, 87, 87);
  font-size: 20px;
  text-transform: capitalize;
}

.scrollUp {
  width: 46px;
  height: 46px;
  position: fixed;
  right: 40px;
  bottom: 40px;
  background-color: rgb(43, 43, 43);
  z-index: 10;
  text-align: center;
  border: 2px solid rgb(43, 43, 43);
  color: #fff;
  font-size: 1.5em;
}
.scrollUp:hover {
  background-color: rgb(255, 255, 255);
  border: 2px solid rgb(43, 43, 43);
  color: rgb(24, 24, 24);
}
.hideIt {
  display: none;
}

.hidden-list {
  display: none;
  position: fixed;
  right: 0px;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgb(29, 29, 29, 0.96);
  z-index: 20;
  padding-top: 15%;
}
.hidden-list a {
  display: block;
  text-align: center;
  color: rgb(255, 255, 255);
  text-decoration: none;
  padding: 40px 30px;
  text-transform: capitalize;
}
.close {
  color: aliceblue;
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 2em;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body {
  font-family: lucida sans;
  position: relative;
  background-color: #fff;
  color: #484848;
  font-family: "Roboto", sans-serif;
}
.section-header {
  font-size: 60px;
  text-transform: capitalize;
  text-align: center;
  font-size: 36px;
  padding: 20px 0;
  color: #333;
}
h1,
h2,
h3,
h4 {
  text-transform: capitalize;
}
li {
  list-style: none;
}
section {
  padding: 50px 0;
}

